/**
 * Enhances multi-select fields to allow selection without CTRL key.
 *
 * @module     local_equipment/multiselect_enhancer
 * @copyright  2024 Your Name <your@email.com>
 * @license    http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */

import $ from 'jquery';

/**
 * Initialize the module.
 */
export const init = () => {
    $('select[multiple]').each((index, element) => {
        enhanceMultiSelect(element);
    });
};

/**
 * Enhance a multi-select element to allow selection without CTRL key.
 * @param {HTMLElement} selectElement - The select element to enhance.
 */
const enhanceMultiSelect = (selectElement) => {
    $(selectElement)
        .on('mousedown', (e) => {
            e.preventDefault();

            const option = $(e.target).closest('option');
            if (option.length) {
                option.prop('selected', !option.prop('selected'));
                $(selectElement).trigger('change');
            }
        })
        .on('mousemove', (e) => {
            e.preventDefault();
        });
};
